const stripePromise = loadStripe('pk_test_51IDpHFIRiN8NewytzC0856E85t72Bd2zF2Kn7sWSRgrZS8HlcQmCQNdEbXDqLrFIXuPJIf5arF9ojL7V5l32hpvL00UaiQd0BI')
let memberCount = 777;
let userKey = 'PASSWORD';

function percent(members, tierAmt, tierPrev) {
  if (members<tierPrev) return 0;
  else if (members>tierAmt) return 100;
  else return (members-tierPrev)/(tierAmt-tierPrev)*100;
}

function progressText(members, tierAmt, tierPrev) {
  let dollars=0;
  if (tierAmt<=100) dollars=50;
  else if (tierAmt<=500) dollars=100;
  else if (tierAmt<=1500) dollars=150;
  else dollars=200;

  let amtFilled=0
  if ((members-tierPrev) > (tierAmt-tierPrev)) amtFilled = tierAmt-tierPrev  
  else amtFilled = members-tierPrev;

  if (percent(members, tierAmt, tierPrev) >= 60) return amtFilled + ' / ' + (tierAmt-tierPrev) + ' slots filled for $' + dollars + '/mo';
  else if (percent(members, tierAmt, tierPrev) >= 40) return amtFilled + ' / ' + (tierAmt-tierPrev) + ' slots filled';
  else if (percent(members, tierAmt, tierPrev) >= 20) return amtFilled + ' / ' + (tierAmt-tierPrev);
  else return ' ';
}

function signUpText(members) {
  let dollars=0;
  let tier=0;
  let prevTier=0;
  if (members<100) {dollars=50; tier=100; prevTier=0;}
  else if (members<500) {dollars=100; tier=400; prevTier=100;}
  else if (members<1500) {dollars=150; tier=1000; prevTier=500;}
  else if (members<3000) {dollars=200; tier=1500; prevTier=1500}
  else return 'All slots filled! Grab some popcorn while you watch us win :)'

  return tier + prevTier - members + ' slots open for $' + dollars + '/mo pricing';
}

function showProg(percent) {
  if(percent === 0) return false;
  else return true; 
}

function App() {
  const [count, setCount] = useState(memberCount);
  const [key, setKey] = useState(userKey);
  const [isLoading, setLoading] = useState(false);

  const checkoutClick = async (event) => {
    setLoading(true);
    // Get Stripe.js instance
    const stripe = await stripePromise;
    // Call your backend to create the Checkout Session
    const response = await axios.post('https://alertigo-api.herokuapp.com/create-checkout-session');
    // console.log(response);
    const session = await response.data;

    // When the customer clicks on the button, redirect them to Checkout.
    const result = await stripe.redirectToCheckout({
      sessionId: session.id,
    });
    if (result.error) {
      // If `redirectToCheckout` fails due to a browser or network
      // error, display the localized error message to your customer
      // using `result.error.message`.
      console.log(result.error.message);
    }
  }

  useEffect(() => {
    if (isLoading) {
      checkoutClick().then(() => {
        setLoading(false);
      });
    }
  }, [isLoading]);


  return (
    <Container fluid className='p-0'>
      {/* NAVBAR */}
      <Navbar bg='secondary' expand='xl' sticky='top' className='shadow-lg text-left justify-content-left'>
        <Navbar.Brand className='ml-xl-auto'>
          <Jumbotron className='bg-secondary p-0 mb-0'>
            <h1 className='text-dark mb-0'>ALERTIGO</h1>
            <p className='d-none d-xl-block text-dark mb-0'>LEARN TO TRADE 1% BETTER THAN THE COMPETITION</p>
          </Jumbotron>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="main-nav"/>
        <Navbar.Collapse id="main-nav">
          <Nav className='text-dark ml-auto'>
            <Nav.Link href='http://twitter.com/alertigo'>TWITTER</Nav.Link>
            <Nav.Link href='http://facebook.com/alertigo'>FACEBOOK</Nav.Link>
            <Nav.Link href='http://instagram.com/alertigo'>INSTAGRAM</Nav.Link>
          </Nav>
          {/* COUNTER
          <Navbar.Text>
            <input className='text-dark ml-3' style={{width: '2.5em', background: 'none', border: 'none'}} type='text' value={count} onChange={(e) => setCount(e.target.value)} />
            <span className='text-dark'>MEMBERS & COUNTING</span>
          </Navbar.Text> */}
          <Form inline>
            <FormControl type="text" placeholder="Search" className="m-3" value={count} onChange={(e) => setCount(e.target.value)} />
            <FormControl type="text" placeholder="Search" className="m-3" value={key} onChange={(e) => setKey(e.target.value)} />
            <Button variant="outline-dark">Sign In</Button>
          </Form>
        </Navbar.Collapse>
      </Navbar>
      {/* ALERTS */}
      <AlertsList/> 
      {/* WHO TF ARE YOU */}
      <TeamBio/>
      {/* WHAT IS TRADING */}
      <TradingInfo/>
      {/* SIGN UP SECTION */}
      <Navbar bg='secondary' className='shadow-lg'>
        <Container className='justify-content-center'>
          <Row className='justify-content-center p-5'>
            <Col xs={12} className='text-center'>
              <Button size='lg' variant='primary' className='shadow text-dark p-4' disabled={isLoading} onClick={!isLoading ? checkoutClick : null}>
                <h1 className='font-weight-light'>{isLoading ? 'LOADING...' : 'SUBSCRIBE TO ALERTIGO'}</h1>
                <h4>{isLoading ? "You're making a great choice" : signUpText(count)}</h4>
              </Button>
            </Col>
            <Row className='justify-content-center'>
              <Col xs={showProg(percent(count, 100, 0)) ? 12 : 0} md={showProg(percent(count, 100, 0)) ? 6 : 0} className={showProg(percent(count, 100, 0)) ? 'visible' : 'invisible progInvisible'}>
                <ProgressBar className='shadow-lg' now={percent(count, 100, 0)} label={progressText(count, 100, 0)}/>
              </Col>
              <Col xs={showProg(percent(count, 500, 100)) ? 12 : 0} md={showProg(percent(count, 500, 100)) ? 6 : 0} className={showProg(percent(count, 500, 100)) ? 'visible' : 'invisible progInvisible'}>
                <ProgressBar className='shadow-lg' now={percent(count, 500, 100)} label={progressText(count, 500, 100)}/>
              </Col>
              <Col xs={showProg(percent(count, 1500, 500)) ? 12 : 0} md={showProg(percent(count, 1500, 500)) ? 6 : 0} className={showProg(percent(count, 1500, 500)) ? 'visible' : 'invisible progInvisible'}>
                <ProgressBar className='shadow-lg' now={percent(count, 1500, 500)} label={progressText(count, 1500, 500)}/>
              </Col>
              <Col xs={showProg(percent(count, 3000, 1500)) ? 12 : 0} md={showProg(percent(count, 3000, 1500)) ? 6 : 0} className={showProg(percent(count, 3000, 1500)) ? 'visible' : 'invisible progInvisible'}>
                <ProgressBar className='shadow-lg' now={percent(count, 3000, 1500)} label={progressText(count, 3000, 1500)}/>
              </Col>
            </Row>
          </Row>
        </Container>
      </Navbar>
    </Container>
  );
}

export default App;
